/* General Styles */
body {
    margin: 0;
    padding-top: 80px; /* cukup untuk navbar */
    background: linear-gradient(135deg, #ee859a, #ff99ac);
    font-family: 'poppins', sans-serif;
    min-height: 100vh;
    overflow: visible;
}


/* Navigation */
/* Navbar */
.navbar {
    background: #584b4b;
    padding: 15px 20px;
    text-align: center;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.navbar ul {
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
}

.navbar ul li {
    margin: 0 15px;
}

.navbar ul li a {
    color: white;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    padding: 8px 15px;
    transition: 0.3s;
}

.navbar ul li a:hover {
    background: #ff3388;
    border-radius: 5px;
}
@media screen and (max-width: 768px) 
{
    .navbar{
        flex-direction:column;
        align-items: center;
        padding: 10px;
    }
    .navbar ul{
        flex-direction: column;
        text-align: center;
    }
    .navbar ul li {
        margin: 10px 0;

    }
    
}
.page-wrapper {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
  }

/* Section Styles */
section {
    padding: 20px 20px;
    text-align: center;
}

/* Home Page */
/* Styling untuk halaman Home */
   
section.home {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        padding-top: 50px; /* biar tidak ketutupan navbar */
        background: linear-gradient(to bottom,#f3f3f3,#d1c3c3);
     }
     .profile-card {
        background: #ffffffcc;
        width: 380px;
        text-align: center;
        padding: 30px;
        border-radius: 25px;
        box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
        transition: all 0.3s ease-in-out;
    }
    
    .profile-card:hover {
        transform: translateY(-5px);
        box-shadow: 0px 15px 35px rgba(0, 0, 0, 0.15);
    }
    .profile-card h2 {
        font-family: 'Poppins', sans-serif;
        font-size: 24px;
        margin-bottom: 5px;
      }
      
      .profile-card p {
        font-size: 14px;
        color: #555;
        line-height: 1.6;
      }
    .profile-header {
        height: 90px;
        background: linear-gradient(90deg, #ee859a, #ff99ac);
        border-radius: 10px 10px 0 0;
    }
    
    .profile-pic {
        width: 120px;
        height: 120px;
        border-radius: 50%;
        margin-top: -50px;
        border: 4px solid #fffffff5;
    }
    
    h2 {
        color: #333;
        margin: 10px 0 5px;
    }
    
    p {
        color: #333;
        font-size: 14px;
    }
    .profile-description {
        max-width: 600px;
        margin: 30px auto;
        padding: 20px;
        background: #ffffffcc;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
        text-align: justify;
        transition: all 0.3s ease-in-out;
      }
      
      .profile-description h3 {
        color: #333;
        margin-bottom: 10px;
      }
      
      .profile-description p {
        font-size: 16px;
        line-height: 1.6;
        color: #444;
      }
      .profile-description:hover {
        box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
      }
      .profile-description p + p {
        margin-top: 12px;
      }
/* Friends Page */
.friends {
    background: linear-gradient(to bottom,#f3f3f3,#d1c3c3);
    margin-top: 20px;
    padding: 20px 20px;
    text-align: center;
}

.friends h1 {
    font-size: 32px;
    color: #1976d2;
}

.friends p {
    font-size: 18px;
    color: #3d3939;
}

/* Blog Page */
.blog-container {
    width: 80%;
    margin: auto;
    text-align: center;
    background: linear-gradient(to bottom,#f3f3f3,#d1c3c3);
}

.blog-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Buat kolom otomatis */
    gap: 20px;
    margin-top: 20px;
}

.blog-card {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    transition: 0.3s;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.blog-card:hover {
    background-color: #fff3e0;
    border-color: #ff9800;
}

.blog-card h2 a {
    text-decoration: none;
    color: #ff5722; 
}

.blog-date {
    color: #777;
    font-size: 14px;
}

.blog-excerpt {
    font-size: 16px;
    color: #333;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Kolom otomatis */
    gap: 10px; /* Jarak antar kolom */
    padding: 20px;
    text-align: left;
    list-style-position: inside; /* Menjadikan nomor tetap sejajar dengan teks */
    
}

.grid-container li {
    background-color: #faf5f5;
    padding: 10px;
    border-radius: 5px;
    font-weight: bold;  
    transition: 0.3s;
}

.grid-container li a {
    text-decoration: none;
    color: #333;
}

.grid-container li:hover {
    background-color: #f58da3;
}

.grid-container li:hover a {
    color: white

}